import { Component, OnInit, PipeTransform } from '@angular/core';
import { ApiService } from '../../shared/services/api.service';
import { routerTransition } from '../../router.animations';
import { Observable } from 'rxjs';
import { FormControl } from '@angular/forms';
import { DecimalPipe } from '@angular/common';
import { map, startWith } from 'rxjs/operators';

@Component({
    selector: 'app-inventory',
    templateUrl: './inventory.component.html',
    styleUrls: ['./inventory.component.scss'],
    animations: [routerTransition()]
})
export class InventoryComponent implements OnInit {
    itemsList: Item[] = new Array(); // Observable<Item[]>;
    filter = new FormControl('');

    listOfInventory: any[];
    constructor(private apiService: ApiService, private pipe: DecimalPipe) {
        this.apiService.itemList().subscribe((data: any) => {
            this.itemsList = data;
        });

        this.filter.valueChanges.pipe(
            startWith(''),
            map((text) => search(this.itemsList, text, pipe))
        );
    }

    ngOnInit() {}
}

interface Item {
    id: any;
    itemName: string;
    itemDescription: string;
    itemPrice: number;
    itemImageUri: any;
    op_flag: string;
}

function search(itemsList: Item[], text: string, pipe: PipeTransform): Item[] {
    return itemsList.filter((item) => {
        const term = text.toLowerCase();
        return item.itemName.toLowerCase().includes(term) || pipe.transform(item.itemPrice).includes(term);
    });
}
